<script>

  import { push } from 'svelte-spa-router'

  let email = ''
  let password = ''

  function emailValidator(context) {
    let re = /\S+@\S+\.\S+/
    return re.test(context)
  }

  function emailParser(context) {
    let em = context.split('@')
    return em[0]
  }

  function login (e) {
    if (e.keyCode === 13) {
      if (email === '') {
        let event = new CustomEvent('show-toast', {detail: {title: 'Email kosong !', time: 2000, icon: 'icon-cross'}})
        document.dispatchEvent(event)
      }else if (password === '') {
        let event = new CustomEvent('show-toast', {detail: {title: 'Password kosong !', time: 2000, icon: 'icon-cross'}})
        document.dispatchEvent(event)
      }else if (email !== '' && password !== '') {
        if (emailValidator(email) === true) {

          if (emailParser(email) === 'admin') {
            let event = new CustomEvent('show-toast', {detail: {title: 'Selamat Datang Admin', time: 2000, icon: 'icon-check'}})
            document.dispatchEvent(event)

            push(`/admin`)
          }else if (emailParser(email) === 'user') {
            let event = new CustomEvent('show-toast', {detail: {title: 'Selamat Datang', time: 2000, icon: 'icon-check'}})
            document.dispatchEvent(event)

            push(`/user`)
          }
        }else {
          let event = new CustomEvent('show-toast', {detail: {title: 'Email tidak valid !', time: 2000, icon: 'icon-cross'}})
          document.dispatchEvent(event)
        }
      }
    }
  }
</script>

<div class="login-page">
  <div class="column col-12">
    <div class="login-wrapper">
      <div class="login-card">

        <div class="login-form">
          <div class="l-form__wrapper d-flex">
            <label for="login-email">
              <i class="icon icon-mail"></i>
            </label>
            <input type="email" id="login-email" bind:value={ email } on:keypress={ login }>
          </div>

          <div class="l-form__wrapper d-flex">
            <label for="login-password">
              <i class="icon icon-apps"></i>
            </label>
            <input type="password" id="login-password" bind:value={ password } on:keypress={ login }>
          </div>
        </div>

      </div>
    </div>
  </div>
</div>

<style>
  .login-page {
    background-color: #24242b;
    display: block;
    color: white;
    height: 100vh;
    width: 100%;
  }

  .login-page .login-wrapper {
    justify-content: center;
    align-items: center;
    display: flex;
    height: 100vh;
  }

  .login-page .login-wrapper .login-card {
    background-color: #31313a;
    border-radius: 5px;
    padding: 20px;
  }

  .login-page .login-wrapper .login-card .l-form__wrapper {
    align-items: center;
    padding: 18px 30px;
  }

  .login-page .login-wrapper .login-card .l-form__wrapper label {
    margin-right: 30px;
    display: inherit;
  }

  .login-page .login-wrapper .login-card .l-form__wrapper label i {
    font-size: 22px;
  }

  .login-page .login-wrapper .login-card .l-form__wrapper input {
    background-color: #3d3d46;
    border-radius: 3px;
    padding: 6px 12px;
    color: white;
    border: none;
  }

</style>